* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}



.maincontainer {
  background-image: linear-gradient(180deg, skyblue, skyblue, whitesmoke, white);
  background-attachment: fixed;
  display: flex;
  flex-wrap: wrap;

}


/* navbar */

.navcontainer {
  height: 10vh;
  width: 100vw;
  position: fixed;
  z-index: 10;
  background-color: rgb(108, 108, 108);

}

.navheader {
  display: flex;
  top: 0;
  width: 100vw;
  align-items: center;
  justify-content: flex-end;
}

.logo {
  scale: 5;
  color: rgb(86, 207, 255);
  position: absolute;
  left: 100px;
}

.item {
  margin: 15px;
  font-size: 20px;
  color: gainsboro;
}

.item:hover {
  transition: 0.2s;
  cursor: pointer;
  color: rgb(64, 201, 255);
}

.item4 {
  margin: 15px 25px 15px 15px;
}



/* header content */

.homecontent {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.name {
  font-size: 40px;
  font-style: italic;
}

.content1 {
  font-size: 65px;
  margin: 100px 0;
  padding: 20px 0px;
}

.content2 {
  margin: 40px 0px 0px 0px;
  font-size: 23px;
}

.content3 {
  background-color: white;
  box-shadow: 6px 6px 12px #c5c5c5;
  color: black;
  margin: 30px 0px 0px 0px;
  padding: 10px 15px;
  border-radius: 15px;
}

.content3:hover {
  cursor: pointer;
  transition: 0.2s;
  background-color: rgb(186, 235, 254);
  color: black;
}



/* About */
.aboutcontainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
}


.aboutcontent {
  width: 100vw;
  font-size: 20px;
  padding: 10px;
}

.aboutcontent p:nth-child(1) {
  font-size: 40px;
  padding: 0px 0px 30px 400px;
}

.aboutcontent p:nth-child(2) {
  padding: 10px 20px;
}

.aboutcontent p:nth-child(3) {
  padding: 10px 20px;
}

.aboutcontent p:nth-child(4) {
  padding: 10px 20px;
}

.aboutcontent p:nth-child(5) {
  font-size: 30px;
  padding: 10px 0;
}

.aboutcontent p:nth-child(6) {
  padding: 10px 80px;
}

.aboutcontent p:nth-child(7) {
  font-size: 30px;
  padding: 10px 0;
}

.aboutcontent p:nth-child(8) {
  padding: 0px 80px;
}




/* portfolio */

.portmain {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
}

.portfoliohead {
  font-size: 40px;
  margin-top: 120px;

}

.portfoliocontent {
  margin: 0px 250px;
}

img {
  height: 400px;
  width: 800px;
}



/* contact */

.contactcontainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  position: relative;
}

.cont1 {
  font-size: 40px;
  margin: 0px 0px 0px 60px;
  padding: 10px 0px;
}

.cont2 {
  margin: 20px 0px 10px 0px;
  font-size: 20px;
}


.icons {
  /* width:100%; */
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 130px;
}

.icon {
  scale: 5;
  color: slategrey
}

.linkedin:hover {
  transition: 0.2s;
  color: blue;
}

.insta:hover {
  transition: 0.2s;
  color: rgb(205, 72, 107);
}

.whatsapp:hover {
  transition: 0.2s;
  color: rgb(20, 210, 20);
}

.github:hover {
  transition: 0.2s;
  color: black
}

.gotop {
  scale: 2;
  margin-bottom: 20px;
  color: slategrey;
}

.gotop:hover {
  transition: 0.2s ease-in-out;
  color: black;
  scale: 2.5;
}



.footercontainer {
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  color: bisque;
  width: 100%;
  height: 10vh;
}

.footercontainer:hover {
  transition: 0.2s;
  color: rgb(64, 201, 255);
}




